.c{
    height: 100vh;
    position: relative;
}
.c-bg{
    width: 20px;
    height: 100%;
    background-color: #697A21;
    position: absolute;
}
.c-wrapper {
    padding: 50px;
    display: flex;
}
.c-title{
    font-size:60px;
}
.c-info-item{
    display: flex;
    align-items: center;
    margin: 40px 0px;
    font-weight: 300;
}
.c-icon{
    width: 40px;
    height: 40px;
    margin-right: 10px;
}
.c-left{
    flex:1;
}
.c-right{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
form{
    margin-top: 20px;
}
input{
    width: 50%;
    height: 50px;
    border:none;
    border-bottom: 1px solid black;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}
textarea{
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}
button{
    border:none;
    padding: 15px;
    background-color: #94849B;
    color: white;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
    border-radius: 50px;
}
a{
    text-decoration: none;
    color:black;
    display:flex;
    align-items: center;
   
}

@media screen and (max-width: 480px){
    .c-wrapper{
        flex-direction: column;
        padding: 0px 50px;
    }
    .c-title{
        font-size: 30px;
    }
    .c-info-item{
        margin: 20px 0px;
        width: 100%;
    }
    form{
        margin-top: 0px;
        display: flex;
        flex-wrap: wrap;
        padding-bottom: 10px;
    }
    input {
        width: 30%;
        height: 40px;
        margin: 10px;
        margin-left: 0;
    }

}